import _ from 'lodash';
import Api from '@/api';
import routes, { routeConstructor } from '@/api/routes';
import ApiParamsPresenter from '@/presenters/ApiParamsPresenter';
import ResultPresenter from '@/presenters/ResultPresenter';
import types from './mutation-types';

export const reset = (context) => {
  const { commit } = context;

  commit(types.ITEMS_RESET);
};

export const getItemsList = (context, data) => {
  const { commit, dispatch } = context;
  const { db, namespace, params } = data;

  commit(types.ITEMS_GET_LIST.REQUEST);

  return new Promise((resolve, reject) => {
    Api.get(routeConstructor(routes.items, db, namespace), ApiParamsPresenter.transform(params))
      .then(
        (response) => {
          const result = _.get(response, 'data', {});
          const payload = {
            list: ResultPresenter.items(result),
            total: ResultPresenter.totalItems(result),
          };
          commit(types.ITEMS_GET_LIST.SUCCESS, payload);
          resolve();
        },
        (error) => {
          commit(types.ITEMS_GET_LIST.FAIL, error);
          dispatch('settings/throwError', error, { root: true });
          reject();
        },
    ).catch((error) => {
      commit(types.ITEMS_GET_LIST.FAIL, error);
      dispatch('settings/throwError', error, { root: true });
      reject();
    });
  });
};

export const getItemsListByQuery = (context, data) => {
  const { commit, dispatch } = context;

  const { db, params } = data;

  commit(types.ITEMS_GET_LIST_BY_QUERY.REQUEST);

  return new Promise((resolve, reject) => {
    Api.get(routeConstructor(routes.query, db), ApiParamsPresenter.transform(params))
      .then(
        (response) => {
          const result = _.get(response, 'data', {});
          const payload = {
            list: ResultPresenter.items(result),
            total: ResultPresenter.totalItems(result),
          };
          commit(types.ITEMS_GET_LIST_BY_QUERY.SUCCESS, payload);
          resolve();
        },
        (error) => {
          commit(types.ITEMS_GET_LIST_BY_QUERY.FAIL, error);
          dispatch('settings/throwError', error, { root: true });
          reject();
        },
    ).catch((error) => {
      commit(types.ITEMS_GET_LIST_BY_QUERY.FAIL, error);
      dispatch('settings/throwError', error, { root: true });
      reject();
    });
  });
};

export const deleteItem = (context, pload) => {
  const { commit, dispatch } = context;
  const { db, namespace, item } = pload;

  commit(types.ITEMS_DELETE.REQUEST);

  return new Promise((resolve, reject) => {
    Api.delete(routeConstructor(routes.items, db, namespace), item)
      .then(
        () => {
          commit(types.ITEMS_DELETE.SUCCESS);
          dispatch('settings/showSuccessMessage', 'Item is deleted', { root: true });
          dispatch('getItemsList', { db, namespace }).then(() => resolve());
        },
        (error) => {
          commit(types.ITEMS_DELETE.FAIL, error);
          dispatch('settings/throwError', error, { root: true });
          reject();
        },
    ).catch((error) => {
      commit(types.ITEMS_DELETE.FAIL, error);
      dispatch('settings/throwError', error, { root: true });
      reject();
    });
  });
};

