import Vue from 'vue';
import Router from 'vue-router';

import NotFoundPage from '@/pages/NotFoundPage';
import MainPage from '@/pages/MainPage';
import NamespacesPage from '@/pages/NamespacesPage';
import ItemsPage from '@/pages/ItemsPage';
import QueriesPage from '@/pages/QueriesPage';

import PropsPresenter from '@/presenters/PropsPresenter';
import webroutes from './webroutes';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.assetsPublicPath,
  routes: [
    {
      path: '*',
      name: 'NotFoundPage',
      component: NotFoundPage,
    },
    {
      path: webroutes.root,
      name: 'MainPage',
      component: MainPage,
    },
    {
      path: webroutes.namespaces,
      name: 'NamespacesPage',
      component: NamespacesPage,
    },
    {
      path: webroutes.items,
      name: 'ItemsPage',
      component: ItemsPage,
      props: route => PropsPresenter.makeForItemsPage(route.query),
    },
    {
      path: webroutes.queries,
      name: 'QueriesPage',
      component: QueriesPage,
      props: route => PropsPresenter.makeForQueriesPage(route.query),
    },
  ],
});



