<template>
  <default-layout>
    <div slot="page">
      <preloader class="preloader" :isLoading='isLoading' />

      <b-container v-if="database" fluid class="mb-4">
          <b-row>
            <b-col cols="11">
              <b-form-textarea
                class="queryarea"
                v-model="params.q"
                placeholder="Query"
                :rows="3"
                :max-rows="6"
                :disabled="!database"
                @keyup.native.ctrl.enter="onQuery" />
            </b-col>
            <b-col cols="1">
              <b-button-group>
                <b-button :disabled="!database" @click.stop="onQuery">Execute</b-button>
                <b-dropdown :disabled="!isAnyItemInQueryHistory" ref="queryselect">
                  <b-dropdown-item v-for="query, index in queryHistory" :key="index" @click.stop="onQuerySelect(query)">{{query}}</b-dropdown-item>
                </b-dropdown>
              </b-button-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <b-alert class="mt-3" v-if="!database && !isLoading" show>Create a database</b-alert>
            </b-col>
          </b-row>
          <b-row class="mt-4">
            <b-col>
              <b-button-group size="sm">
                <b-button :disabled="isLocked" variant="outline-primary" :pressed="params.viewType === 'list'" @click="onViewTypeChange('list')">List</b-button>
                <b-button :disabled="isLocked" variant="outline-primary" :pressed="params.viewType === 'table'" @click="onViewTypeChange('table')">Table</b-button>
              </b-button-group>
            </b-col>

            <b-col>
              <center>
                <strong>{{paginationInfo}}</strong>
              </center>
            </b-col>

            <b-col>
              <b-pagination :disabled="isLocked" size="sm" class="float-right ml-2" hide-ellipsis hide-goto-end-buttons :limit="0" :total-rows="totalItems"
                  :per-page="params.pageSize" v-model="params.page" v-on:change="onPageChange">
              </b-pagination>

              <b-dropdown :disabled="isLocked" size="sm" variant="primary" class="float-right" :text="String(params.pageSize)">
                <b-dropdown-item
                  v-for="pageSize, index in pageSizeVariants"
                  :key="index"
                  @click="onPageSizeChange(pageSize)">{{pageSize}}
                </b-dropdown-item>
              </b-dropdown>
            </b-col>

          </b-row>
      </b-container>

      <b-container v-if="params.viewType === 'list'" fluid>
        <b-row v-for="item, index in items" :key="index" fluid>
          <b-col>
            <b-card>
              <tree-view :data="item" :options="{modifiable: false, maxDepth: 1, rootObjectKey: jsonRootName(item)}" />
            </b-card>
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="params.viewType === 'table'" fluid>
        <b-row>
          <b-col>
            <data-table
              v-if="itemsSize"
              class="mt-1"
              :items="items"
              :columns="fields"
              :selectedFields="selectedFields"
            />
          </b-col>
        </b-row>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapActions, mapGetters, mapState } from 'vuex';
import DefaultLayout from '@/layouts/DefaultLayout';
import DataTable from '@/components/DataTable';
import Preloader from '@/components/Preloader';
import defaultParams, { PAGE_SIZE_VARIANTS } from '@/consts/defaultParams';

export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    DataTable,
    Preloader,
  },

  props: {
    db: {
      type: String,
      default: defaultParams.db,
    },
    page: {
      type: Number,
      default: defaultParams.page,
    },
    pageSize: {
      type: Number,
      default: defaultParams.pageSize,
    },
    viewType: {
      type: String,
      default: defaultParams.viewType,
    },
    q: {
      type: String,
      default: defaultParams.q,
    },
  },

  data() {
    const props = this.$props;

    return {
      params: {
        ...props,
      },
    };
  },

  computed: {
    ...mapState({
      queryHistory: state => state.settings.queryHistory,
      database: state => state.settings.database,
      items: state => state.items.qList,
      totalItems: state => state.items.qTotal,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      fields: 'items/qFields',
      itemsSize: 'items/qItemsSize',
    }),

    selectedFields() {
      return _.cloneDeep(this.fields);
    },

    isAnyItemInQueryHistory() {
      return !_.isEmpty(this.queryHistory);
    },

    isLocked() {
      return !this.totalItems;
    },

    paginationInfo() {
      const totalItemsInfo = this.totalItems;

      if (!totalItemsInfo) {
        return 'No items found';
      }

      return `${totalItemsInfo} item(s)`;
    },

    pageSizeVariants() {
      return PAGE_SIZE_VARIANTS;
    },
  },

  methods: {
    ...mapActions({
      selectDatabase: 'settings/selectDatabase',
      pushQueryToHistory: 'settings/pushQueryToHistory',
      loadQueriesFromHistory: 'settings/loadQueriesFromHistory',
      getItemsListByQuery: 'items/getItemsListByQuery',
    }),

    jsonRootName(item) {
      if (_.isEmpty(item[this.fields.id])) {
        const firstField = this.fields[0];

        return `${firstField}_${item[firstField]}`;
      }

      return `${this.fields.id}_${item.id}`;
    },

    onQuery() {
      if (!_.isEmpty(this.$refs.queryselect)) {
        this.$refs.queryselect.visible = false;
      }

      const selectedDb = this.database || this.params.db;
      if (!selectedDb) {
        return;
      }

      if (!this.database) {
        this.selectDatabase(selectedDb);
      }

      this.params.db = this.database;
      this.params.q = this.params.q.trim();

      const queryParams = _.pickBy(this.params);
      const actionParams = {
        db: this.database,
        params: {
          ...queryParams,
        },
      };

      const query = _.mapKeys(queryParams, (value, key) => _.snakeCase(key));

      if (this.params.q) {
        this.pushQueryToHistory({ query: this.params.q });

        this.getItemsListByQuery(actionParams).then(() => {
          this.$router.push({ path: this.$router.path, query });
        });
      }
    },

    onQuerySelect(query) {
      this.params.q = query;

      this.onQuery();
    },

    onViewTypeChange(viewType) {
      this.params.viewType = viewType;

      this.onQuery();
    },

    onPageSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.params.page = 1;

      this.onQuery();
    },

    onPageChange(page) {
      this.params.page = page;

      this.onQuery();
    },
  },

  mounted() {
    this.loadQueriesFromHistory();
    this.onQuery();
  },
};
</script>

<style scoped>
.editor {
  font-size: 20px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



