import defaultParams from '@/consts/defaultParams';

export default {
  limit(apiparams) {
    const { pageSize } = apiparams;

    return pageSize || defaultParams.limit;
  },

  offset(apiparams) {
    const { page, pageSize } = apiparams;

    return (page - 1) * pageSize || defaultParams.offset;
  },

  sortOrder(apiparams) {
    const { sortOrder } = apiparams;

    return sortOrder || defaultParams.sortOrder;
  },

  sortField(apiparams) {
    const { sortField } = apiparams;

    return sortField || defaultParams.sortField;
  },

  filter(apiparams) {
    const { filter } = apiparams;

    return filter || defaultParams.filter;
  },

  q(apiparams) {
    const { q } = apiparams;

    return q || defaultParams.q;
  },

  transform(apiparams = {}) {
    const preparedApiParams = Object.keys(apiparams).reduce((previousParamsAcc, currentField) => {
      const fields = {
        ...previousParamsAcc,
      };

      if (currentField === 'pageSize') {
        fields.limit = this.limit(apiparams);
      } else if (currentField === 'page') {
        fields.offset = this.offset(apiparams);
      } else if (currentField === 'sortField') {
        fields.sortField = this.sortField(apiparams);
      } else if (currentField === 'sortOrder') {
        fields.sortOrder = this.sortOrder(apiparams);
      } else if (currentField === 'filter') {
        fields.filter = this.filter(apiparams);
      } else if (currentField === 'q') {
        fields.q = this.q(apiparams);
      } else {
        fields[currentField] = apiparams[currentField];
      }

      return fields;
    }, {});

    return preparedApiParams;
  },
};



