<template>
  <default-layout>
    <div slot="page">
      <preloader :isLoading="isLoading" />

      <b-alert show v-if="!selectedDb && !isLoading">Create a database</b-alert>
      <b-alert show v-if="selectedDb && !selectedNs">Select a namespace</b-alert>

      <b-container v-if="selectedDb && selectedNs" fluid class="mb-4">
          <b-row>
              <b-col>
                <b-input-group size="sm">
                  <b-input size="sm" v-model="params.filter" @keyup.native.enter="onFindClick"/>
                  <b-button variant="success" size="sm" @click="onFindClick">Find</b-button>
                </b-input-group>
              </b-col>
          </b-row>
          <b-row class="mt-4">
            <b-col>
              <b-button-group size="sm">
                <b-button variant="outline-primary" :pressed="params.viewType === 'list'" @click="onViewTypeChange('list')">List</b-button>
                <b-button variant="outline-primary" :pressed="params.viewType === 'table'" @click="onViewTypeChange('table')">Table</b-button>
                <b-button variant="success" :pressed="params.viewType === 'editor'" @click.prevent="onViewTypeChange('editor')">Editor</b-button>
              </b-button-group>
            </b-col>

            <b-col>
              <center>
                <strong>{{paginationInfo}}</strong>
              </center>
            </b-col>

            <b-col>
              <b-pagination size="sm" class="float-right ml-2" hide-ellipsis hide-goto-end-buttons :limit="0" :total-rows="totalItems"
                  :per-page="params.pageSize" v-model="params.page" v-on:change="onPageChange">
              </b-pagination>

              <b-dropdown size="sm" variant="primary" class="float-right" :text="String(params.pageSize)">
                <b-dropdown-item
                  v-for="pageSize in pageSizeVariants"
                  v-bind:key="pageSize"
                  @click="onPageSizeChange(pageSize)">{{pageSize}}
                </b-dropdown-item>
              </b-dropdown>
            </b-col>

          </b-row>
      </b-container>

      <b-container v-if="params.viewType === 'list' && itemsSize" fluid>
        <b-row v-for="item, index in items" :key="index">
          <b-col>
            <b-card>
              <tree-view :data="item" :options="{modifiable: false, maxDepth: 1, rootObjectKey: jsonRootName(item)}" />
            </b-card>
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="params.viewType === 'table' && itemsSize" fluid>
        <b-row>
          <b-col>
            <data-table
              v-if="selectedNs"
              class="mt-1"
              isWithActions
              isUpdateAllowed
              isRemoveAllowed
              isCreateAllowed
              :title="selectedNs"
              :items="items"
              :itemTemplate="itemTemplate"
              :columns="columns"
              :selectedFields="columnsFromSettings"
              :sortableFields="sortableFields"
              :sortField="params.sortField"
              :sortOrder="params.sortOrder"
              :onSelectedFieldsChange="onSelectedFieldsChange"
              :onSortChange="onSortChange"
              :onItemDelete="onItemDeleteQuestion"
              :onItemUpdate="onItemUpdateQuestion"
            />
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="params.viewType === 'editor'" fluid>
        <b-row>
          <b-col>
            <b-card>
              <editor ref="editor" class="editor" lang="json" theme="chrome" width="100%" :height="clHeight" :value="itemTemplate"  />
              <center>
                <b-btn @click="onEditorSave" class="mt-3" variant="success" size="md">Save</b-btn>
              </center>
            </b-card>
          </b-col>
        </b-row>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapActions, mapState, mapGetters } from 'vuex';
import Octicon from 'vue-octicon/components/Octicon';
import Editor from 'vue2-ace-editor';
import DefaultLayout from '@/layouts/DefaultLayout';
import Modal from '@/components/Modal';
import Preloader from '@/components/Preloader';
import DataTable from '@/components/DataTable';
import defaultParams, { PAGE_SIZE_VARIANTS } from '@/consts/defaultParams';

export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    Modal,
    Preloader,
    DataTable,
    Octicon,
    Editor,
  },

  props: {
    db: {
      type: String,
      default: defaultParams.db,
    },
    namespace: {
      type: String,
      default: defaultParams.namespace,
    },
    page: {
      type: Number,
      default: defaultParams.page,
    },
    pageSize: {
      type: Number,
      default: defaultParams.pageSize,
    },
    sortField: {
      type: String,
      default: defaultParams.sortField,
    },
    sortOrder: {
      type: String,
      default: defaultParams.sortOrder,
    },
    filter: {
      type: String,
      default: defaultParams.filter,
    },
    viewType: {
      type: String,
      default: defaultParams.viewType,
    },
  },

  data() {
    const props = this.$props;

    return {
      params: {
        ...props,
      },
      jsonEditor: '',
      clHeight: 500, // FIXME
    };
  },

  computed: {
    ...mapState({
      view: state => state.settings.view,
      dbUsed: state => state.settings.database,
      namespaceUsed: state => state.settings.namespace,
      columnsSelected: state => state.settings.namespacesSelectedFields,
      items: state => state.items.list,
      totalItems: state => state.items.total,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      indexesFields: 'namespaces/indexesNames',
      sortableFields: 'namespaces/sortableFields',
      itemTemplate: 'namespaces/itemTemplate',
      columns: 'items/fields',
      itemsSize: 'items/itemsSize',
      itemsJson: 'items/itemsJson',
    }),

    selectedDb() {
      return this.db || this.dbUsed;
    },

    selectedNs() {
      return this.namespace || this.namespaceUsed;
    },

    columnsFromSettings() {
      const dbData = _.get(this.columnsSelected, this.db, null);

      if (!dbData) {
        return this.indexesFields;
      }

      return _.get(dbData, this.namespace, this.indexesFields);
    },

    isLocked() {
      return !this.totalItems;
    },

    paginationInfo() {
      const totalItemsInfo = this.totalItems || this.itemsSize;

      if (!totalItemsInfo) {
        return 'No items found';
      }

      return `${totalItemsInfo} item(s)`;

      // const totalPages = Math.ceil(totalItemsInfo / this.params.pageSize);
      // const finishInfo = totalPages === this.params.page
      //   ? totalItemsInfo
      //   : this.params.page * this.params.pageSize;

      // const start = (finishInfo - this.pageSize) + 1;
      // const startInfo = start > 0 ? start : 1;

      // return `${startInfo}..${finishInfo} of ${totalItemsInfo}`;
    },

    pageSizeVariants() {
      return PAGE_SIZE_VARIANTS;
    },
  },

  methods: {
    ...mapActions({
      selectDatabase: 'settings/selectDatabase',
      selectNamespace: 'settings/selectNamespace',
      getItemsList: 'items/getItemsList',
      getNamespaceOne: 'namespaces/getNamespaceOne',
    }),

    jsonRootName(item) {
      if (!this.indexesFields[0]) {
        return `${this.namespace}`;
      }

      if (_.isEmpty(item)) {
        return `${this.namespace}_${this.indexesFields[0]}`;
      }

      if (_.isEmpty(this.indexesFields.id)) {
        return `${this.namespace}_${this.indexesFields[0]}_${item[this.indexesFields[0]]}`;
      }

      return `${this.namespace}_${this.indexesFields.id}_${item[this.indexesFields.id]}`;
    },

    onSelectedFieldsChange(fields) {
      this.$store.dispatch('settings/setSelectedFieldsForNamespace', { db: this.db, namespace: this.namespace, fields });
    },

    onQuery() {
      this.params.db = this.selectedDb;
      this.params.namespace = this.selectedNs;

      const queryParams = _.pickBy(this.params);
      const actionParams = {
        db: this.selectedDb,
        namespace: encodeURIComponent(this.selectedNs),
        params: {
          ...queryParams,
        },
      };

      const query = _.mapKeys(queryParams, (value, key) => _.snakeCase(key));
      this.getItemsList(actionParams).then(() => {
        this.$router.push({ path: this.$router.path, query });
      });
    },

    onItemDeleteQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the item:',
        strongMessage: itemPretty,
        event: 'onItemDelete',
        eventParams: itemPretty,
      });
    },

    onItemCreateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will create a new item:',
        strongMessage: itemPretty,
        event: 'onItemCreate',
        eventParams: itemPretty,
      });
    },

    onItemUpdateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will change the existed item:',
        strongMessage: itemPretty,
        event: 'onItemUpdate',
        eventParams: itemPretty,
      });
    },

    onViewTypeChange(viewType) {
      this.params.viewType = viewType;

      this.onQuery();
    },

    onPageSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.params.page = 1;

      this.onQuery();
    },

    onPageChange(page) {
      this.params.page = page;

      this.onQuery();
    },

    onSortChange(sortParams) {
      const { sortField, sortOrder } = sortParams;

      this.params.sortField = sortField;
      this.params.sortOrder = sortOrder;

      this.onQuery();
    },

    onEditorSave() {
      try {
        const item = JSON.parse(this.$refs.editor.contentBackup);
        this.onItemCreateQuestion(item);
      } catch (e) {
        _.noop();
      }
    },

    onFindClick() {
      this.onQuery();
    },
  },

  mounted() {
    this.selectDatabase(this.selectedDb);
    this.selectNamespace(this.selectedNs);

    if (this.selectedDb && this.selectedNs) {
      this.getNamespaceOne({ db: this.selectedDb, namespace: this.selectedNs }).then(() => {
        this.onQuery();
      });
    }
  },
};
</script>

<style scoped>
.editor {
  font-size: 20px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



