import _ from 'lodash';

export default {
  items(data) {
    return _.get(data, 'items', []);
  },

  totalItems(data) {
    const total = _.get(data, 'total_items', 0);

    return total;
  },

  fields(items) {
    if (_.isEmpty(items)) {
      return [];
    }

    const allFields = items.reduce((currFieldsSet, item) => {
      const itemFieldsSet = _.keys(item);

      return [...currFieldsSet, ...itemFieldsSet];
    }, _.keys(items[0]));

    return [...new Set(allFields)];
  },

  errorDescription(error) {
    return (
      _.get(error, 'response.data.description', '') ||
      _.get(error, 'response', '') ||
      'Error is occurred'
    );
  },
};



