import _ from 'lodash';
import ResultPresenter from '@/presenters/ResultPresenter';

export default {
  fields: (state) => {
    const items = _.get(state, 'list', []);

    return ResultPresenter.fields(items);
  },

  qFields: (state) => {
    const items = _.get(state, 'qList', []);

    return ResultPresenter.fields(items);
  },

  itemsSize: (state) => {
    const items = _.get(state, 'list', []);

    return _.size(items);
  },

  qItemsSize: (state) => {
    const items = _.get(state, 'qList', []);

    return _.size(items);
  },

  itemsJson: (state) => {
    const items = _.get(state, 'list', []);

    return JSON.stringify(items);
  },

  qItemsJson: (state) => {
    const items = _.get(state, 'qList', []);

    return JSON.stringify(items);
  },
};



