import _ from 'lodash';
import humps from 'humps';
import defaultParams from '@/consts/defaultParams';

export default {
  db(params) {
    return !_.isEmpty(params.db) ? decodeURIComponent(params.db) : defaultParams.db;
  },

  namespace(params) {
    return !_.isEmpty(params.namespace)
      ? decodeURIComponent(params.namespace)
      : defaultParams.namespace;
  },

  filter(params) {
    return !_.isEmpty(params.filter) ? decodeURIComponent(params.filter) : defaultParams.filter;
  },

  q(params) {
    return !_.isEmpty(params.q) ? decodeURIComponent(params.q) : defaultParams.q;
  },

  viewType(params) {
    return !_.isEmpty(params.viewType)
      ? decodeURIComponent(params.viewType)
      : defaultParams.viewType;
  },

  page(params) {
    return parseInt(params.page, 10) || defaultParams.page;
  },

  pageSize(params) {
    return parseInt(params.pageSize, 10) || defaultParams.pageSize;
  },

  sortOrder(params) {
    return parseInt(params.sortOrder, 10) || defaultParams.sortOrder;
  },

  sortField(params) {
    return parseInt(params.sortField, 10) || defaultParams.sortField;
  },

  makeForItemsPage(params) {
    const camelizedParams = humps.camelizeKeys(params);

    return {
      db: this.db(camelizedParams),
      namespace: this.namespace(camelizedParams),
      page: this.page(camelizedParams),
      pageSize: this.pageSize(camelizedParams),
      sortField: this.sortField(camelizedParams),
      sortOrder: this.sortOrder(camelizedParams),
      viewType: this.viewType(camelizedParams),
      filter: this.filter(camelizedParams),
    };
  },

  makeForQueriesPage(params) {
    const camelizedParams = humps.camelizeKeys(params);

    return {
      db: this.db(camelizedParams),
      page: this.page(camelizedParams),
      pageSize: this.pageSize(camelizedParams),
      viewType: this.viewType(camelizedParams),
      q: this.q(camelizedParams),
    };
  },
};



